@font-face {
  font-family: 'graphik';
  src: url('fonts/graphik-regular-webfont.eot'); /* IE9 Compat Modes */
  src: url('fonts/graphik-regular-webfont.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('fonts/graphik-regular-webfont.woff') format('woff'), /* Pretty Modern Browsers */
       url('fonts/graphik-regular-webfont.ttf')  format('truetype'), /* Safari, Android, iOS */
       url('fonts/graphik-regular-webfont.svg#svgFontName') format('svg'); /* Legacy iOS */
}

@font-face {
  font-family: 'gotham';
  src: url('fonts/gotham-medium-webfont.eot'); /* IE9 Compat Modes */
  src: url('fonts/gotham-medium-webfontt.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('fonts/gotham-medium-webfont.woff') format('woff'), /* Pretty Modern Browsers */
       url('fonts/gotham-medium-webfont.ttf')  format('truetype'), /* Safari, Android, iOS */
       url('fonts/gotham-medium-webfont.svg#svgFontName') format('svg'); /* Legacy iOS */
}